@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* === Google Fonts ===*/
* {
  padding: 0px;
  margin: 0px;
}

/* ==== Typography ====*/
:root {
  --body-font: "Raleway", sans-serif;
  --big-font-size: 2rem;
  --medium-font-size:1rem;
  --normal-font-size:0.938rem;
}

@media screen and (min-width: 768px) {
  :root {
    --big-font-size: 3.5rem;
    --medium-font-size:2rem;
    --normal-font-size:1.2rem;
  }
}

::before,
::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  box-sizing: border-box;
}

body {
  font-family: "Raleway", sans-serif;
  font-family: var(--body-font);
  font-size: 0.938rem;
  font-size: var(--normal-font-size);
  color: #08e9cb;
  background-color: #7ba8db;
}

.link_button {
  text-decoration: none;
  padding: 1rem 2.5rem;
  border-radius: .3rem;
}

.link_button:hover {
  text-decoration: none;
}

.nav {
  position: fixed;
  max-width: 100vw;
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  box-shadow: none;
  background-color: #10151a;
  transition: all 0.3s ease 0s;
  z-index: 10;
}

@media only screen and (max-width: 600px) {
  .nav {
    display: none;
  }
}

.nav .nav_content {
  padding: 0 45px;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  max-width: 1440px;
}

.nav .nav_content .nav_inner_wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  color: white;
}

.nav .nav_list {
  display: flex;
  align-items: center;
  list-style: none;
  margin-bottom: 0;
}

.nav .nav_item {
  position: relative;
  cursor: pointer;
  margin-top: 15px;
  font-weight: 500;
}

@media only screen and (min-width: 600px) {
  .nav .nav_item {
    margin-left: 35px;
    margin-top: 0;
  }
}

@media only screen and (min-width: 600px) {
  .nav .nav_item {
    font-size: 1rem;
  }
}

.nav .nav_item :hover {
  text-decoration: none;
  color: aquamarine;
}

.nav .nav_item .nav_link {
  color: #cccccc;
  position: relative;
  display: inline-block;
  overflow: hidden;
}

.nav .nav_item .nav_link::before, .nav .nav_item .nav_link::after {
  content: "";
  position: absolute;
  transition: transform 0.5s ease 0s;
  right: 0px;
  bottom: 0px;
  background: #0603b9;
}

.nav .nav_item .nav_link::before {
  width: 100%;
  height: 2px;
  transform: translate(101%);
}

.nav .nav_item .nav_link::after {
  height: 100%;
  width: 2px;
  transform: translateY(-100%);
}

.nav .nav_item .nav_link span {
  line-height: 24px;
  display: block;
  padding: 8px;
  text-decoration: none;
}

.nav .nav_item .nav_link span::before, .nav .nav_item .nav_link span::after {
  content: "";
  position: absolute;
  transition: transform 0.5s ease 0s;
  left: 0px;
  top: 0px;
  background: aquamarine;
}

.nav .nav_item .nav_link span::before {
  width: 100%;
  height: 2px;
  transform: translate(101%);
}

.nav .nav_item .nav_link span::after {
  height: 100%;
  width: 2px;
  transform: translateY(-100%);
}

.nav .nav_item .nav_link:hover::before, .nav .nav_item .nav_link:hover::after, .nav .nav_item .nav_link:hover > span::before, .nav .nav_item .nav_link:hover > span::after {
  transform: translate(0px, 0px);
}

.nav .nav_item .active-section {
  overflow: hidden;
  color: aquamarine;
}

.nav .nav_item .active-section::before, .nav .nav_item .active-section::after, .nav .nav_item .active-section > span::before, .nav .nav_item .active-section > span::after {
  transform: translate(0px, 0px);
}

.scroll {
  box-shadow: #00ffff 0px 0px 10px;
  background-color:  #10151a;
}

header {
  height: 110vh;
  width: 100%;
  position: relative;
}



header .header_social {
  position: absolute;
  bottom: 10vh;
  display: flex;
  flex-wrap: wrap;
  font-size: 2rem;
  padding: 1rem;
  flex-direction: row;
}

header .header_social a {
  padding: 1rem;
  color: aquamarine;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #10151a;
}

.overlay .greet-block {
  top: 160px;
  padding: 20px;
  position: absolute;
  font-size: 1rem;
  font-size: var(--medium-font-size);
  font-weight: bold;
  width:100%;
}

@media only screen and (min-width: 600px) {
  .overlay .greet-block {
    padding-left: 10rem;
  }
}

.overlay .greet-block .greet {
  font-size: 2rem;
  font-size: var(--big-font-size);
  font-weight: bold;
}

.overlay .greet-block .greet .greet-title_color {
  color: aquamarine;
}

/* Section Styling */
.section-title {
  color: #cccccc;
  position: relative;
  font-weight: bold;
  font-size: 2rem;
  font-size: var(--big-font-size);
  margin-bottom: 2rem;
  width: 100%;
}

@media only screen and (max-width: 1100px) {
  .section-title {
    font-size: 2rem;
  }
}

@media only screen and (max-width: 600px) {
  .section-title {
    text-align: center;
  }
}

.section-title::before, .section-title::after {
  position: absolute;
  pointer-events: none;
}

.section-title::before {
  content: '';
  width: 10rem;
  border-bottom: 2px solid aquamarine;
  bottom: 0;
  opacity: 0.4;
}

@media only screen and (max-width: 600px) {
  .section-title::before {
    width: 20%;
  }
}

.section-title::after {
  content: attr(data-title);
  top: 0;
  right: 0;
  opacity: 0.14;
  font-size: 10rem;
  font-weight: 700;
  color: #93b5f1;
  letter-spacing: 0.3rem;
  line-height: 1;
}

@media only screen and (max-width: 1100px) {
  .section-title::after {
    font-size: 8rem;
    letter-spacing: 0;
  }
}

@media only screen and (max-width: 600px) {
  .section-title::after {
    display: none;
  }
}

/* About Styling */
.about {
  background-color:  #10151a;
  padding: 30px;
}

.about .about_description {
  font-size: 1rem;
  font-size: var(--medium-font-size);
}

.about .link_button {
  font-size: 1rem;
  font-size: var(--medium-font-size);
  color: aquamarine;
  border: 1px solid #555;
  transition: all .5s ease;
}

/* Skills Styling */
.skills {
  background-color:  #10151a;
  padding: 30px;
}

.skills .skills_container .skills_list {
  list-style: none;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 2.5rem;
}

.skills_heading {
  font-size: 2.5rem;
  font-weight: bold;
  text-align: center;
}

@media only screen and (max-width: 1100px) {
  .skills_heading {
    font-size: 1.5rem;
  }
}

.skills_item {
  padding: 1rem 1rem;
  text-align: center;
}

.skills_item img, .skills_item i {
  display: block;
  font-size: 4rem;
  padding-bottom: 1rem;
}


/* Project styling */



.projects-container {
  background-color:  #10151a;
  padding: 30px;
}

.projects-container .projects {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 100%;
      grid-template-columns: 100%;
  grid-gap: 1.5rem;
  gap: 1.5rem;

}

@media only screen and (min-width: 1100px) {
  .projects-container .projects {
    -ms-grid-columns: (1fr);
        grid-template-columns: repeat(2, 1fr);
  }
}

.projects-container .projects .project {
  overflow: hidden;
  border-radius: 1rem;

}
.projects-container .projects .project .cover {
  position: relative;
}

.projects-container .projects .project .cover .name {
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  box-sizing: border-box;
  padding: 3rem 1rem 1rem;
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.68), rgba(0, 0, 0, 0.933));
}

.projects-container .projects .project .cover .name h2 {
  font-weight: bold;
}

.projects-container .projects .project .cover .links {
  display: flex;
  justify-content: space-around;
}

.projects-container .projects .project .cover .links a {
  color: #cccccc;
}

.projects-container .projects .project .description {
  background: #121212;
  padding: 1rem 1rem;
  height: 100%;
}

.projects-container .projects .project img {
  width: 100%;
  height: 300px;
}



/* End of project styling */

/* Contact Styling */

.contact {
  background-color: #10151a;
  padding: 30px;
}

.contact p {
  font-size: 1rem;
  font-size: var(--medium-font-size);
  text-align: center;
}

.contact .contact_social {
  bottom: 10vh;
  display: flex;
  flex-wrap: wrap;
  font-size: 2rem;
  padding: 1rem;
  flex-direction: row;
}

.contact .contact_social a {
  padding: 1rem;
  color: aquamarine;
}

.contact .link_button {
  font-size: 1rem;
  font-size: var(--medium-font-size);
  color: aquamarine;
  border: 1px solid #555;
  transition: all .5s ease;
  margin-right: 2rem;
}

.footer {
  background-color: #050505;
  text-align: center;
}

.footer .copyright {
  margin-bottom: 0;
  font-size: 0.938rem;
  font-size: var(--normal-font-size);
  padding: 10px;
}
